
.main-content {
    width: 100%;
    margin: 0 auto;
    max-width: 640px;
    padding: 16px;
}
.header-wrapper {
    background: linear-gradient(90deg, hotpink, cyan);
    padding-top: 4px;
    display: flex;
}
header {
    padding: 16px;
    display: flex;
    width: 100%;
    justify-content: center;
    position: relative;
    background-color: white;
}
.header_bg {
    bottom: 0;
    top: 0;
    position: absolute;
    right: -18px;
    width: 50px;
    background-color: white;
    transform: skewX(-20deg);
}
.full-name {
    margin: 0;
    font-size: 20px;
}
.img-wrapper {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
}
.img-wrapper img {
    width: 100%;
}
.text-wrapper {
    flex: 1;
    align-self: center;
    padding-left: 8px;
}
article {
    margin: 32px auto;
}
h1, h2, h3, h4, h5, h6 {
    margin: 8px 0;
}
.summary-paragraph {
    margin-bottom: 24px;
}
ul {
    margin: 0;
}
.experience__employer {
    margin: 0;
}
.experience__rank {
    margin: 0;
}
